.title {
  font-size: 5em;
}

/* ===== HERO ===== */
.hero {
  padding: 10em 2em;
  position: relative;
  display: flex;
  flex-flow: column;
}
.hero .content {
  margin: auto;
} .hero .content h3 {
  margin: 2em;
}

#particles {
  position: absolute;
  top: 0;
  left:0;
  padding: 1em;
  width: 100%;
  height: 100%;
  z-index: -1;
}
#particles * {
  animation: reveal 2.5s ease forwards;
}
@keyframes reveal {
  from { opacity: 0; }
  to { opacity: 1; }
}

/* ===== ABOUT ===== */
.about {
  background-color: #ff0000;
  padding: 2em;
}
.about .content h2 {
  margin-right: 1em;
}
.about .content {
  max-width: 750px;
}
@media (min-width: 600px) {
  .about .content {
    display: flex;
    flex-direction: row;
  } 
  .about .content p {
    margin: 2em;
  }
}

/* ===== VENUE ===== */
.venue {
  padding: 8em 2em;
}


.home-venue {
  padding: 4em;
}

.partners {
  display: flex;
  flex-flow: row;
  flex-wrap: wrap;
  padding: 2em;
}
.partner {
  display: flex;
  flex-direction: column;
  max-width: 30%;
  margin: 0.5em auto;
}
.partner img {
  height: 8em;
  width: auto;
  object-fit: contain;
  margin: 1em;
  transition: filter 0.25s ease;
  filter: grayscale();
}
.partner img:active, img:focus, img:hover {
  transition: filter 0.25s ease;
  filter: none;
}
.partner p, .partner h3 {
  margin: 0.25em 0;
  text-align: center;
}
@media (max-width: 600px) {
  .partner {
    max-width: 50%;
  }
  .partner img {
    height: 5em;
  }
}