/* --------------------------------------- */

.spinner {
  position: absolute;
  border: 2px solid #ffffff6f; /* Light grey */
  border-top: 2px solid #fff; /* White */
  border-radius: 50%;
  width: 12px;
  height: 12px;
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite;
}
@-webkit-keyframes spin {
  0% { 
    -webkit-transform: rotate(0deg); 
            transform: rotate(0deg); 
  }
  100% { 
    -webkit-transform: rotate(360deg); 
            transform: rotate(360deg); 
  }
}
@keyframes spin {
  0% { 
    -webkit-transform: rotate(0deg); 
            transform: rotate(0deg); 
  }
  100% { 
    -webkit-transform: rotate(360deg); 
            transform: rotate(360deg); 
  }
}

/* ============================================================================= */

header {
  z-index: 50;
}
header .container {
  padding: 1em;
  margin: auto auto;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  max-width: 1200px;
  display: flex;
  flex-direction: row;
  position: relative;
}

.hamburger {
  display: block;
  margin: auto 0 auto auto;
  height: 100%;
  padding: 1em;
  padding-right: 0.5em; 
  justify-self: center;
  align-self: flex-end;
  z-index: 11000;
}
.hamburger span,
.hamburger span::before,
.hamburger span::after {
  display: block;
  position: relative;
  background: #fff;
  height: 2px;
  width: 25px;
  -webkit-transition: all 0.25s ease;
  transition: all 0.25s ease;
}
.hamburger span::before,
.hamburger span::after {
  content: '';
  position: absolute;
}
.hamburger span::before {
  bottom: 7px;
}
.hamburger span::after {
  top: 7px;
}

#sidebar-toggle:checked ~ .hamburger {
  position: fixed;
  top: 1.85em;
  right: 1em;
}
#sidebar-toggle:checked ~ .hamburger span::before {
  bottom: 0;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
} #sidebar-toggle:checked ~ .hamburger span {
  background: transparent;
} #sidebar-toggle:checked ~ .hamburger span::after {
  top: 0;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

/* LOGO ----------------------------- */
header .logo {
  font-size: 1.5em;
  margin: 0.5em 0.25em;
}

/* SIDEBAR MENU --------------------- */
.sidebar {
  position: fixed;
  height: 100vh;
  width: 0;
  top: 0;
  right: 0;
  opacity: 0;
  z-index: 10000;
  max-width: 300px;
  background: -webkit-gradient(linear, left top, left bottom, from(#ff0000), to(#aa0000));
  background: -webkit-linear-gradient(#ff0000, #aa0000);
  background: linear-gradient(#ff0000, #aa0000);
  -webkit-transition: all 0.25s ease;
  transition: all 0.25s ease;
} #sidebar-toggle:checked ~ .sidebar {
  opacity: 1;
  width: 60%;
}

/* SIDEBAR SHADOW */
.sidebar-shadow {
  position: fixed;
  display: none;
  opacity: 0;
  animation: show 0.25s ease reverse;
  width: 100vw;
  height: 100vh;
  margin: -1em;
  z-index: 9000;
} #sidebar-toggle:checked ~ .sidebar-shadow {
  display: block;
  opacity: 0.6;
  background-color: #000;
  -webkit-animation: show 0.5s ease;
          animation: show 0.5s ease;
} @-webkit-keyframes show {
  from { opacity: 0 } to { opacity: 0.6 }
} @keyframes show {
  from { opacity: 0 } to { opacity: 0.6 }
}

/* NAVIGATION */
header .sidebar-nav {
  display: flex;
  flex-direction: column;
  margin: 8em 10em;
  width: 50%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  justify-self: center;
  align-self: flex-end;
  text-align: left;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
} header .sidebar-nav a {
  color: #fff;
  margin: 1em;

  font-weight: 400;
  text-decoration: none;
  text-transform: uppercase;
}

#sidebar-toggle:checked ~ .sidebar li {
  display: block;
  opacity: 1;
} #sidebar-toggle:checked ~ .sidebar .sidebar-nav {
  margin: 8em 2em;
}

/* DESKTOP MENU --------------------- */

.desktop-nav {
  display: none;
}
@media (min-width: 800px) {
  header .container {
    flex-direction: column;
  }
  header .sidebar {
    display: none;
    right: -60%;
  } #sidebar-toggle:checked ~ .sidebar {
    display: none;
  } 
  header .sidebar-nav {
    display: none;
  } 
  header .hamburger {
    display: none;
  }

  header .logo {
    font-size: 3em;
    margin: 2rem;
  }

  header .desktop-nav {
    display: block;
    margin: auto auto;
    display: inline-block;
    font-weight: 400;
  } header .desktop-nav a {
    color: #707070;
    margin: 2em;
  
    font-weight: 400;
    text-decoration: none;
    text-transform: uppercase;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
  } header .desktop-nav a:hover {
    color: #fff;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
  }
}

